.tooltip {
    background-color: var(--bg-color);
    border: 1px solid var(--gray-regular);
    border-radius: 8px;
    padding: 6px 10px 6px 8px;
    position: absolute;
    bottom: calc(100% + 6px);
    left: 20px;
    z-index: 100;
    display: flex;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: all .8s ease-in-out;
    &::after, &::before {
        content: '';
        position: absolute;
        top: 100%;
        left: 14px;
        border: 5px solid transparent;
        border-top: 6px solid var(--gray-regular);
    }

    &::after {
        border-top: 6px solid var(--bg-color);
        top: calc(100% - 1px);
    }
    &-img {
        margin-right: 8px;
    }
    &-descr {
        line-height: 1.38rem;
        font-weight: 500;
        width: 81px;
    }
}

.tooltip.active-tooltip {
    visibility: visible;
    opacity: 1;
}
